.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 350px;

  @include vp-1919 {
    padding: 0 110px;

    &--form {
      padding: 0 119px;
    }
  }

  @include vp-1439 {
    padding: 0 140px;
  }

  @include vp-1023 {
    padding: 0 50px;
  }

  @include vp-767 {
    padding: 0 25px;

    &--header {
      padding: 0;
    }
  }
}
